@use '../base/variables' as v;
@use '../base/mixins' as m;

.galeria {
    @include m.contenedor;
    &-imagenes {
    
        @include m.grid(1,2rem);

        @include m.telefono {
            @include m.grid(2,2rem);
        }

        @include m.tablet {
            @include m.grid(3,2rem);
        }

        @include m.desktop {
            @include m.grid(4,2rem);
        }

        img {
            cursor: pointer;
            height: 20rem;
            object-fit: cover; // Notes css
        }
    }
}

.modal {
    z-index: 200;
    position: fixed;
    inset: 0;
    margin: 0;
    background: rgba(v.$negro, 0.5);
    display: grid;
    place-items: center; // NOTES css

    animation: fadeIN ease-in 0.5s;

    &.fade-out{
        animation: fadeOut ease-in 520ms;
    }

    img {
        max-width: 60vw;
    }
}

.btn-cerrar {
    width: 4rem;
    height: 4rem;
    margin: 0;

    border-radius: 50%;
    background-color: v.$blanco;
    border: none;
    color: v.$negro;
    display: grid;
    place-items: center;
    font-weight: 700;
    cursor: pointer;

    transition: transform 0.05s ease-in-out;

    &:hover{
        transform: scale(1.1);
        animation-name: cerrar;
        animation-duration: 0.9;
        animation-fill-mode: forwards;
    }

}

@keyframes cerrar {
    0%{
        background-color: v.$blanco;
    }
    100%{
        background-color: v.$rosa;
    }
}

@keyframes fadeIN {
    0%{
        opacity: 0;
    }
    100%{
        opacity: 1;
    }
}

@keyframes fadeOut {
    0%{
        opacity: 1;
    }
    100%{
        opacity: 0;
    }
}