@use '../base/variables' as v;
@use '../base/mixins' as m;
@use 'sass:color';

.video{
    position: relative;
    min-height: 40rem;
    max-height: 60rem;
    overflow: hidden;
    .overlay {
        display: flex;
        align-items: center;
        position: absolute;
        background: linear-gradient(
            270deg,
            rgba(v.$morado,60%),
            rgba( color.adjust(v.$morado, $lightness: -20%) ,90%),
        );
        inset: 0;
    }
    video {
        display: block;
        width: auto;
        @include m.desktop{
            width: 100%;
        }
    }
}

.contenido-video{
    z-index: 10;
    h2 {
        color: v.$blanco;
        font-size: 7rem;
        margin: 0;
        padding: 0;
    }
    p {
        display: inline-block;
        background-color: v.$rosa;
        padding: 1rem 2rem;
        color: v.$blanco;
        font-size: 2.4rem;
        font-weight: 700;
        margin:0;
    }
}